import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { BankAccountService } from 'src/app/api/controllers/BankAccount';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-bank-settings',
  templateUrl: './bank-settings.component.html',
  styleUrls: ['./bank-settings.component.scss']
})
export class BankSettingsComponent implements OnInit {

  bankAccountData:any;

  isBankLoaded:boolean = false;
  isBankSubmitted:boolean = true;

  isBankNew:boolean = false;
  isBankUpdate:boolean = false;
  
  alertMessage:string = "";
  isError:boolean = false; 
  isSuccess:boolean = false;

  modalService:any;

  bank:any; 

  constructor(
  	 private injector: Injector,
  	public dialog: MatDialog,
  	private bankAccountService: BankAccountService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
  	this.modalService = this.injector.get(ModalService);
  }

  addNewbankAccount(){
  	this.isBankNew = true;
  	this.isBankUpdate = false;
  	this.bank = {};
  }

  updatebankAccount(item){
  	this.isBankUpdate = true;
  	this.isBankNew = false;
  	this.bank = item;
  }

  removeBankBox(){
  	this.isBankNew = false;
  	this.isBankUpdate = false;
  	this.isError = false;
  }


  showConfirmModal(id) {
    this.modalService.showConfirmModal(id);
  }

  showBankAccountInfo(acc){

  }

  submitbankAccountForm(form) {

  	this.isBankSubmitted = false;

  	if(this.isBankNew) {

  		let details = {

		  "apartmentId": parseInt(this.cookieService.get('apartmentId')),
		  "bankName": this.bank.bankName,
		  "branchName": this.bank.branchName,
		  "accountNumber": this.bank.accountNumber,
		  "accountHolderName": this.bank.accountHolderName,
		  "ifsc": this.bank.ifsc,
		  "isPrimary": this.bank.isPrimary,
		  "isActive": true,
		  "insertedBy": parseInt(this.cookieService.get('userId')),
		  "insertedOn": new Date().toISOString(),
		  "updatedBy": 0,
		  "updatedOn": "2020-03-13T09:38:46.772Z"

		}

		let bankAccountDetails = {
			bankAccountDetails: details
		}

		this.bankAccountService.addBankAccountDetails(bankAccountDetails).subscribe((res:any) => {

	  		if(res.message) {

	  			
	  			this.bankAccountService.getAllBankAccountDetails().subscribe((res:any) => {

	  				this.isBankSubmitted = true;
	  				this.isBankNew = false;
  					this.isBankUpdate = false;
		  			
		  			this.isSuccess = true;
		  			this.sharedService.setAlertMessage("Bank Account Added Successfully");
		          	
		          	this.bankAccountData = res.filter(item => {
	                	return item.isActive && (item.apartmentId == parseInt(this.cookieService.get('apartmentId')));
	              	});	

	  			});

	  			

	  		}
	  		else {

	  			this.isBankSubmitted = true;	
	          	this.isError = true;
	          	this.alertMessage = res.errorMessage;
	  		}

  		});

  	}

  	else {

  		let details = {

  		  "bankAccountDetailsId": this.bank.bankAccountDetailsId,
		  "apartmentId": parseInt(this.cookieService.get('apartmentId')),
		  "bankName": this.bank.bankName,
		  "branchName": this.bank.branchName,
		  "accountNumber": this.bank.accountNumber,
		  "accountHolderName": this.bank.accountHolderName,
		  "ifsc": this.bank.ifsc,
		  "isPrimary": this.bank.isPrimary,
		  "isActive": this.bank.isActive,
		  "insertedBy": parseInt(this.cookieService.get('userId')),
		  "insertedOn": new Date().toISOString(),
		  "updatedBy": parseInt(this.cookieService.get('userId')),
		  "updatedOn": new Date().toISOString()

		}

		let bankAccountDetails = {
			bankAccountDetails: details
		}

		this.bankAccountService.updateBankAccountDetails(bankAccountDetails).subscribe((res:any) => {

	  		if(res.message) {

	  			this.bankAccountService.getAllBankAccountDetails().subscribe((res:any) => {

	  				this.isBankSubmitted = true;
	  				this.isBankNew = false;
  					this.isBankUpdate = false;

		  			this.isSuccess = true;
		  			this.sharedService.setAlertMessage("Bank Account Update Successfully");
		          	
		          	this.bankAccountData = res.filter(item => {
	                	return item.isActive && (item.apartmentId == parseInt(this.cookieService.get('apartmentId')));
	              	});	

	  			});

	  			

	  		}
	  		else {

	  			this.isBankSubmitted = true;	
	          	this.isError = true;
	          	this.alertMessage = res.errorMessage;
	  		}

  		});

  	}

  	

  }

  ngOnInit() {

  	this.bank = {};

  	this.bankAccountService.getAllBankAccountDetails().subscribe((res:any) => {

  		this.isBankLoaded = true;

  		this.bankAccountData = res.filter(item => {
        	return item.isActive && (item.apartmentId == parseInt(this.cookieService.get('apartmentId')));
        });

  	});

  	// delete account
    this.sharedService.unitlistdeleteindexcast.subscribe( id => {

      if(id != null){

      	this.isBankLoaded = false;

	  	var params = {
	      bankAccountDetailsId: id,
	      deleteBy: parseInt(this.cookieService.get('userId'))
	    }

	  	this.bankAccountService.deleteBankAccountDetails(params).subscribe((res:any) => {

	  	  this.bankAccountData = this.bankAccountData.filter((type) => type.bankAccountDetailsId !== id);
		  this.isBankLoaded = true;
		  this.sharedService.setAlertMessage("Bank Account deleted");
          this.sharedService.setUnitListDeleteIndex(null);

		}, error => {
          console.log(error);
        });
        
      }

    });

  }

}
