import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { BankCashComponent } from './bank-cash.component';
import { BankSettingsComponent } from './components/bank-settings/bank-settings.component';

const routes: Routes = [
	{ path: '', component: BankCashComponent},
	{ path: 'bank', component: BankCashComponent},
	{ path: 'settings', component: BankSettingsComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BankCashRoutingModule { }
